.App {
  text-align: left;
  font-family: "Trebuchet MS";
}
#navbar {
  margin: -9px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 50px;
  background-color: #306;
  color: rgb(245, 239, 239);
  padding: 1rem;
}
#logo {
  padding-top: 4px;
}
/*Hacky workaround because I can't get the text to center*/
.companyName {
  font-size: 40px;
  font-weight: bolder;
  color: rgb(245, 245, 245);
  text-decoration: none;
  padding-left: 1rem;
  vertical-align: 15px;
}
#nav-links {
  list-style-type: none;
  display: flex;
  margin-left: 3rem;
  padding: 0;
  font-size: 20px;
}
#nav-links li {
  margin-right: 3rem;
}
.nav-links {
  color: rgb(245, 239, 239);
  text-decoration: none;
  transition-duration: 0.3s;
}
/*hover on nav-links not working*/
a:hover {
  color: rgb(240, 180, 255);
}
#cart-icon {
  filter: invert(95%);
  padding-right: 2rem;
  vertical-align: 12px;
}
#cart-count {
  background-color: #f44336;
  color: #fff;
  border-radius: 100%;
  padding: 0.05rem 0.325rem;
  font-size: 0.9rem;
  position: relative;
  top: -2.7rem;
  right: 2.5rem;
}
/*Blue underline below the cart count won't go away*/
a {
  text-decoration: none;
}
#sign-up-button {
  background-color: rgb(210, 140, 220);
  color: rgb(245, 245, 245);
  font-family: "Trebuchet MS";
  border: none;
  border-radius: 30px;
  padding: 5px 15px 7px 15px;
  font-size: 22px;
  transition-duration: 0.5s;
  vertical-align: 26px;
}
#sign-up-button:hover {
  background-color: rgb(240, 180, 255);
}
#user-icon {
  padding-left: 15px;
  filter: invert(5%);
}
.row {
  display: flex;
  padding-top: 50px;
  align-items: center;
  text-align: center;
  font-weight: bolder;
}
.column {
  flex: 50%;
  text-color: #306;
  margin-left: 50px;
  margin-right: 50px;
}
#download-button {
  background-color: rgb(210, 140, 220);
  color: rgb(245, 239, 239);
  font-family: "Trebuchet MS";
  border-color: ;
  border-radius: 30px;
  padding: 3px 9px 5px 9px;
  font-size: 22px;
  font-weight: bolder;
  transition-duration: 0.5s;
  vertical-align: 26px;
  margin-top: 20px;
}
#download-button:hover {
  background-color: rgb(240, 180, 255);
}
.body-button {
  display: block;
  background-color: #c7aad2;
  color: #306;
  font-family: "Trebuchet MS";
  border: none;
  border-radius: 40px;
  font-size: 30px;
  font-weight: bolder;
  text-shadow: rgb(245, 239, 239) 2px 2px 3px;
  width: 190px;
  height: 210px;
  margin-top: 25px;
  margin-bottom: 50px;
  transition-duration: 0.4s;
}
.body-button:hover {
  box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24),
    0 17px 50px 0 rgba(0, 0, 0, 0.19);
  width: 191px;
  height: 211px;
}
#mapDiv {
  width: 900px;
}

body {
  background-color: rgb(245, 239, 239);
}
